.direction-card {
    position: relative;

    &:hover {}

    &-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &-left {
        &--title {
            font-size: 24px;
            font-weight: 700;
            color: rgba(253, 253, 253, 1);
        }

        &--subtitle {
            font-size: 14px;
            font-weight: 400;
            color: rgba(253, 253, 253, 1);
        }
    }

    &-right {
        font-size: 20px;
        font-weight: 700;
        color: rgba(253, 253, 253, 1);
        padding: 16px 8px;
        border-radius: 4px;
        background: rgba(255, 255, 255, 0.3);
    }

    &__img {
        position: relative;
        width: 380px;
        height: 500px;
        border-radius: 32px;
    }

    &__rating {
        &-pic {}

        &-number {}
    }
}




.direction-card__content {
    position: relative;
    background: rgba(255, 255, 255, 0.2);
    padding: 20px 32px;
    bottom: 133px;

    &-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &-left {
        &--title {
            font-size: 24px;
            font-weight: 700;
            color: rgba(253, 253, 253, 1);
        }

        &--subtitle {
            font-size: 14px;
            font-weight: 400;
            color: rgba(253, 253, 253, 1);
        }
    }

    &-right {
        font-size: 20px;
        font-weight: 700;
        color: rgba(253, 253, 253, 1);
        padding: 16px 8px;
        border-radius: 4px;
        background: rgba(255, 255, 255, 0.3);
    }
}

.direction-card__rating {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    width: 65px;
    height: 32px;
    padding: 7px;
    top: -421px;
    right: -316px;
    background: rgba(253, 253, 253, 0.4);

    &-pic {}

    &-number {
        font-size: 14px;
        font-weight: 700;
        color: rgba(26, 62, 62, 1);
    }
}

.direction-card__back {
    top: 0;
    position: absolute;
    width: 350px;
    padding: 30px;

    &-content {
        background: red;
        margin-bottom: 38px;

        &-wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

        &-left {
            &--title {
                font-size: 24px;
                font-weight: 700;
                color: rgba(253, 253, 253, 1);
            }

            &--subtitle {
                font-size: 14px;
                font-weight: 400;
                color: rgba(253, 253, 253, 1);
            }
        }

        &-right {
            font-size: 20px;
            font-weight: 700;
            color: rgba(253, 253, 253, 1);
            padding: 16px 8px;
            border-radius: 4px;
            background: rgba(255, 255, 255, 0.3);
        }
    }

    &-paragraph {
        margin-bottom: 100px;
font-size: 16px;
font-weight: 400;
color: rgba(253, 253, 253, 1);

    }
}
.direction-card:hover {
    visibility:visible;
}